<template>
  <div>
      <div class="row">
          <div class="offset-2 col-8">

        
      <div class="card">
          <div class="card-header">Edit Text Berjalan</div>
          <div class="card-body">
              <div class="sm-form ">
                  <label for="speed">Your speed</label>
                  <input type="text" id="speed" name="speed" class="form-control form-control-sm" placeholder="speed" v-model="xdata.speed" >
              </div>
              <button type="button" @click="editspeed" class="btn btn-sm btn-primary  "><span class="typcn typcn-edit"></span> Edit Speed</button>
              <form action="" @submit.prevent="edit">
                  <div class="sm-form ">
                      <label for="text">Your text</label>
                      <input type="text" id="text" name="text" class="form-control form-control-sm" placeholder="text" v-model="vdata.text" >
                  </div>
                  <br>
                  <hr class="style16">
                  <button type="submit" class="btn btn-sm btn-primary  ">Edit</button>
                  <br>
                  <p class="p-3 text-xl">
                      {{datanya[0].text}}
                  </p>
              </form>
          </div>
      </div>
  </div>  </div>
      </div>
</template>
<script>
import axios from 'axios'
import firebase from 'firebase'
let db = firebase.firestore()
export default{
    layout:"tv",
  data(){
    return{
        datanya:{},
        vdata:{},
        xdata:{
            speed:0
        },
    }
  },
  methods:{
      edit(){
          db.collection('tv/btn/tvtext').doc('U91xp3dp6sRf1iKyY8z8').set(this.vdata,{merge:true}).then(res=>{
            alert('berhasil di edit')
        })
      },
      editspeed(){
          db.collection('tv/btn/setting').doc('t0ZJNvsrdeMmo7cGrUmy').set(this.xdata,{merge:true}).then(res=>{
            alert('berhasil di edit')
        })
      }
  },
  mounted() {
      db.collection('tv/btn/tvtext').onSnapshot(res=>{
        let data = res.docs.map(e=>e.data())
        this.datanya=data
      })
       db.collection('tv/btn/setting').onSnapshot(res=>{
        let data = res.docs.map(e=>e.data())
        this.xdata.speed=data[0].speed
      })
  },
}
</script>